pre {
    margin: 0;
    white-space: pre-wrap;
    word-wrap: break-word;
}

code {
    margin: 0;
    padding: 0;
}

.Api {
    height: 100%;
    padding: 5px;
}

/* AÐ·i */
.Api_block a {
    font-weight: 600;
    font-size: 15px;
    margin-right: 0;
    color: rgb(0, 0, 125);
}

.Api_block {
    border-bottom: 3px solid black;
    margin-bottom: 25px;
}

.Api_block:first-child h1{
    text-align: center;
}

.Api_block:first-child {
    margin-top: 65px;
}

.Api_block:last-child{
    border-bottom: none;
}


.Api_block h1{
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 700;
}

.Api_block p span {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    padding: 2px;
}

.Api_block ul li{
    margin-bottom: 10px;
}



.Api_elem {
    background-color: rgba(0, 0, 0, 0.2);
    font-weight: 500;
    border: 3px solid black;
    padding: 5px;
    border-radius: 8px;
    margin-bottom: 25px;
}

.Api_block h1, .Api_block h3, .Api_block h4 {
    margin-bottom: 15px;
}
