/* Modificators */
.bgColor {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    padding: 2px;
}

.italic {
    font-style: italic;
}

.fw700 {
    font-weight: 700;
}

/* Container */
.container {
    width: 100%;
    max-width: 1100px;
    height: 100%;
    margin: 0 auto;

    padding: 10px;
}


/* Buttons */
button {
    background: white;
    border: 3px solid black;
    border-radius: 8px;

    color: black;
    font-weight: 700;

    padding: 10px;

    margin-right: 15px;

    font-size: 16px;

    transition: .2s linear;
}


button:last-child{
    margin-right: 0;
}

button:focus {
    outline: none;
}

button:hover {
    background: black;
    color: white;
    cursor: pointer;

    transform: scale(1.1);

    transition: .2s linear;
}

/* Link */
a {
    font-size: 18px;
    color: rgba(0, 0, 0, .7);

    font-weight: 700;

    margin-right: 15px;

    text-decoration: none;

    transition: .2s linear;

    border-bottom: 0 solid white;
}

a:focus {
    outline: none;
}

a.active {
    transition: .2s linear;

    font-size: 18px;
    color: rgba(0, 0, 0, 1) !important;

    border-bottom: 3px solid black;
}

a:last-child {
    margin-right: 0;
}

a:hover {
    transform: scale(1.1);
}

a.active:hover {
    transform: scale(1);
}

@media (max-width: 565px) {
    .Home {
        height: auto;
    }

    .container {
        padding: 2px;
    }
    button {
        margin-right: 0;
        margin-bottom: 5px;
    }
    button:last-child {
        margin-bottom: 0;
    }
}
