/* Buttons */
button.category_elem:hover {
    background: black;
    color: white;
    cursor: pointer;

    transform: scale(1.1);

    transition: .2s linear;
}

button.category_elem:hover .category_elem_describe .title{
    background:rgba(76, 76, 76, 1);

    transition: .2s linear;
}

/*Category*/
.category {
    display: flex;
    justify-content: space-between;

    padding: 25px;

    margin-top: 65px;
}

.category_elem {
    display: flex;
    flex-direction: column;
    align-items: center;

    top: 0;
    width: 30%;
}

.category_elem h1 {
    text-transform: uppercase;
    font-weight: 900;
}

.category_elem_describe {
    text-align: center;
    margin-top: 10px;
}

.title {
    background:rgba(76, 76, 76, 1);
    border:  3px solid black;

    border-bottom: 0;

    border-radius: 8px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    color: white;
    font-weight: 700;

    padding: 5px;
}

.describe {
    background: white;
    border:  3px solid black;

    border-radius: 8px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    color: black;
    font-weight: 700;

    padding: 10px;

    height: 100px;
}


.category_elem_describe h3 {
    padding: 5px;

    background:rgba(76, 76, 76, 1);
    border-bottom: 3px solid black;
    color: white;
    font-weight: 700;
}

.category_elem_describe p {
    padding: 10px;
    margin: 0;

    font-weight: 600;
}


@media (max-width: 991px) {
    .category {
        margin-top: 45px;
    }

    .describe {
        height: 125px;
    }
}

@media (max-width: 767px) {
    .describe {
        font-size: 14px;
        padding: 5px;
        height: 145px;
    }
}

@media (max-width: 565px) {
    .category {
        flex-wrap: wrap;
        justify-content: center;

        padding: 5px;
    }

    .category_elem {
        width: 95%;
        margin-right: 0;
        margin-bottom: 15px;
    }

    .describe {
        height: 100%;
    }

    .category_elem_describe {
        margin-top: 5px;
    }

    .category_elem h1 {
        font-size: 14px;
    }

    button.category_elem {
        padding: 5px;
    }
}

