header {
    position: absolute;
    width: 100%;

    background: rgba(255, 255, 255, 0.9);
    border-bottom: 3px solid black;

    color: black;

    top: 0;
    left: 0;
    right: 0;

    font-weight: 700;

    z-index: 2;
}

.Header_content {
    display: flex;
    justify-content: space-between;
}

.Header_logo {
    display: flex;
    align-items: center;
}

a.Header_logo {
    color: black;
}

.Header_logo img {
    width: 24px;
    height: 24px;
    margin-right: 10px;
}

.Header_logo div {
    font-size: 24px;
    font-weight: 700;
    font-style: italic;
}

.Header_nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/* Links */

a.Header_logo:hover {
    transform: scale(1);
}

.Header_nav a:last-child {
    margin-right: 0;
}

@media (max-width: 565px) {
    .Header_logo div {
        font-size: 20px;
    }

    .Header_logo img {
        width: 18px;
        height: 18px;
    }

    .Header_nav a {
        font-size: 14px;
        margin-right: 5px;
    }
}