    width: 100%;

    background: rgba(255, 255, 255, 0.5);
    border-top: 3px solid black;

    color: black;

    bottom: 0;
    left: 0;

    padding: 10px;
    font-weight: 700;

    z-index: 2;
}

.Footer_content {
    display: flex;
    justify-content: center;
}
