/* Buttons */
button.button_arrow-up, button.button_arrow-down  {
    margin: 0;
    background: none;
    border: none;
}

button.button_arrow-up img, button.button_arrow-down img{
    width: 24px;
}

button.button_arrow-down img{
    transform: rotate(180deg);
}



/*Search*/
.search {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    margin-top: 25px;

    padding: 25px 70px;
}

.search_panel {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.search_panel_title {
    margin-right: 10px;
    font-weight: 700;
    font-size: 15px;
}

.search input {
    background: white;
    border: 3px solid black;
    border-radius: 8px;

    width: 100%;
    max-width: 250px;

    margin-right: 30px;

    padding: 7px;

    font-size: 19px;
}

.search input:focus{
    outline: none;
}

.search input::placeholder {
    font-weight: 700;
    font-style: italic;
}

/* search_result */
.search_result {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    padding: 15px;

    margin-bottom: 50px;
}

.search_calc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 15px;
}

.search_calc_numbers {
    display: flex;
    justify-content: center;

    background: white;
    border: 3px solid black;
    border-radius: 8px;
    padding: 10px;
}

.search_calc_numbers span {
    font-size: 30px;
    font-weight: 700;
    color: white;

    background: rgba(0, 0, 0, 0.8);
    border-radius: 8px;

    padding:0 8px;

    margin-right: 2px;
}

.search_calc_numbers span:last-child {
    margin-right: 0;
}

.search_result_describe {
    width: 70%;

    color: white;
    font-size: 18px;
    font-weight: 700;

    background: rgba(0, 0, 0, 0.7);
    border: 3px solid black;
    border-radius: 8px;

    padding:10px;
}

@media (max-width: 991px) {
    .search_reult_describe {
        width: 60%;
    }

    .Random_Button {
        width: 45%;

        margin-bottom: 15px;
    }
}

@media (max-width: 767px) {
    .search {
        padding: 25px 25px;
    }

    .search_calc_numbers {
        padding: 5px;
    }

    .search_calc_numbers span {
        padding: 0 5px;
    }
}

@media (max-width: 565px) {
    .search {
        padding: 5px;
        margin-top: 0;
    }

    .search_panel {
        flex-direction: column;
        justify-content: center;
    }

    .search_panel_title {
        margin-right: 0;
        margin-bottom: 5px;
    }

    .search input {
        margin-right: 0;
        margin-bottom: 5px;
    }

    .search_result {
        padding: 5px;
    }

    .search_calc {
        margin-right: 5px;
    }

    .search_calc_numbers span {
        font-size: 20px;
        margin-right: 0;
    }

    .search_reult_describe {
        width: 55%;
    }

    .search_result_Buttons {
        width: 100%;
    }

    .Random_Button {
        width: 100%;
    }
}


