html {
  font-size: 15px;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
  color: #000;

  background: url(./img/blue-digits-background-math-concept-numbers-digits-texture-blue-backgrounds.png)  repeat-y;
  background-size: cover;

  position: relative;

  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}



